"use client";

import { useState } from "react"
import { CircleDollarSign, ShoppingBasket, Store, Tag, TrendingUp, Package } from "lucide-react"
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, BarChart, Bar, PieChart, Pie, Cell } from "recharts"

export default function AdminDashboard() {
    const currency = '$'
    const [dashboardData] = useState({
        totalOrders: 1245,
        totalStores: 45,
        totalProducts: 892,
        revenue: 125430.50,
        storeRevenue: [
            { id: '1', name: 'Store A', logo: 'https://via.placeholder.com/40', username: 'storea', revenue: 25430, totalOrders: 234, totalProducts: 45, isActive: true },
            { id: '2', name: 'Store B', logo: 'https://via.placeholder.com/40', username: 'storeb', revenue: 18920, totalOrders: 189, totalProducts: 32, isActive: true },
            { id: '3', name: 'Store C', logo: 'https://via.placeholder.com/40', username: 'storec', revenue: 15600, totalOrders: 156, totalProducts: 28, isActive: true }
        ],
        categoryBreakdown: {
            'Fresh': { count: 320, stores: 15 },
            'Upvas': { count: 245, stores: 12 },
            'Special': { count: 180, stores: 10 }
        },
        ordersByStatus: {
            ORDER_PLACED: 145,
            PROCESSING: 98,
            SHIPPED: 234,
            DELIVERED: 768
        },
        paymentBreakdown: {
            COD: 723,
            STRIPE: 522
        },
        revenueByDate: {
            '12/1/2024': 4520,
            '12/2/2024': 5680,
            '12/3/2024': 4890,
            '12/4/2024': 6234,
            '12/5/2024': 5430
        },
        topStores: [
            { name: 'Store A', revenue: 25430 },
            { name: 'Store B', revenue: 18920 },
            { name: 'Store C', revenue: 15600 }
        ],
        recentActivity: [
            { id: '1', store: 'Store A', total: 345.50, status: 'DELIVERED', date: new Date() },
            { id: '2', store: 'Store B', total: 189.99, status: 'SHIPPED', date: new Date() }
        ]
    })

    const statsCards = [
        { title: 'Total Products', value: dashboardData.totalProducts, icon: ShoppingBasket, color: 'bg-blue-500' },
        { title: 'Total Revenue', value: `${currency}${dashboardData.revenue.toLocaleString()}`, icon: CircleDollarSign, color: 'bg-green-500' },
        { title: 'Total Orders', value: dashboardData.totalOrders, icon: Tag, color: 'bg-purple-500' },
        { title: 'Total Stores', value: dashboardData.totalStores, icon: Store, color: 'bg-orange-500' }
    ]

    const revenueChartData = Object.entries(dashboardData.revenueByDate).map(([date, revenue]) => ({
        date,
        revenue
    }))

    const orderStatusData = Object.entries(dashboardData.ordersByStatus).map(([status, count]) => ({
        name: status.replace('_', ' '),
        value: count
    }))

    const categoryData = Object.entries(dashboardData.categoryBreakdown).map(([category, data]) => ({
        category,
        count: data.count,
        stores: data.stores
    }))

    const COLORS = ['#3b82f6', '#f59e0b', '#8b5cf6', '#10b981']

    return (
        <div className="p-6 space-y-6 max-w-7xl">
            <h1 className="text-2xl font-semibold">
                Admin <span className="text-slate-800">Dashboard</span>
            </h1>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                {statsCards.map((stat, index) => (
                    <div key={index} className="bg-white p-5 rounded-lg shadow-sm border border-slate-200 flex items-center justify-between">
                        <div>
                            <p className="text-sm text-slate-500">{stat.title}</p>
                            <p className="text-2xl font-semibold text-slate-800 mt-1">{stat.value}</p>
                        </div>
                        <div className={`${stat.color} p-3 rounded-full`}>
                            <stat.icon className="text-white" size={24} />
                        </div>
                    </div>
                ))}
            </div>

            {/* Revenue Chart */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Platform Revenue (Last 30 Days)</h2>
                <ResponsiveContainer width="100%" height={300}>
                    <AreaChart data={revenueChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Area type="monotone" dataKey="revenue" stroke="#3b82f6" fill="#3b82f6" fillOpacity={0.2} />
                    </AreaChart>
                </ResponsiveContainer>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Order Status */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Order Status Distribution</h2>
                    <ResponsiveContainer width="100%" height={250}>
                        <PieChart>
                            <Pie
                                data={orderStatusData}
                                cx="50%"
                                cy="50%"
                                labelLine={false}
                                label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                                outerRadius={80}
                                fill="#8884d8"
                                dataKey="value"
                            >
                                {orderStatusData.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                                ))}
                            </Pie>
                            <Tooltip />
                        </PieChart>
                    </ResponsiveContainer>
                </div>

                {/* Category Breakdown */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Products by Category</h2>
                    <ResponsiveContainer width="100%" height={250}>
                        <BarChart data={categoryData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="category" />
                            <YAxis />
                            <Tooltip />
                            <Bar dataKey="count" fill="#10b981" />
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </div>

            {/* Top Stores by Revenue */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                <h2 className="text-lg font-semibold mb-4">Store Performance</h2>
                <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                        <thead className="bg-slate-50">
                            <tr>
                                <th className="px-4 py-3 text-left">Store</th>
                                <th className="px-4 py-3 text-left">Username</th>
                                <th className="px-4 py-3 text-right">Products</th>
                                <th className="px-4 py-3 text-right">Orders</th>
                                <th className="px-4 py-3 text-right">Revenue</th>
                                <th className="px-4 py-3 text-center">Status</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y">
                            {dashboardData.storeRevenue.map((store) => (
                                <tr key={store.id} className="hover:bg-slate-50">
                                    <td className="px-4 py-3">
                                        <div className="flex items-center gap-3">
                                            <img src={store.logo} alt="" className="w-10 h-10 rounded-full" />
                                            <span className="font-medium">{store.name}</span>
                                        </div>
                                    </td>
                                    <td className="px-4 py-3 text-slate-500">@{store.username}</td>
                                    <td className="px-4 py-3 text-right">{store.totalProducts}</td>
                                    <td className="px-4 py-3 text-right">{store.totalOrders}</td>
                                    <td className="px-4 py-3 text-right text-green-600 font-semibold">
                                        {currency}{store.revenue.toLocaleString()}
                                    </td>
                                    <td className="px-4 py-3 text-center">
                                        <span className={`px-3 py-1 rounded-full text-xs ${store.isActive ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                                            {store.isActive ? 'Active' : 'Inactive'}
                                        </span>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>

            {/* Payment Methods & Recent Activity */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Payment Methods</h2>
                    <div className="space-y-4">
                        <div className="border rounded-lg p-4">
                            <div className="flex justify-between items-center">
                                <span className="text-slate-600">Cash on Delivery</span>
                                <span className="text-2xl font-bold text-slate-800">{dashboardData.paymentBreakdown.COD}</span>
                            </div>
                            <div className="mt-2 h-2 bg-slate-100 rounded-full overflow-hidden">
                                <div className="h-full bg-blue-500" style={{width: `${(dashboardData.paymentBreakdown.COD / dashboardData.totalOrders) * 100}%`}}></div>
                            </div>
                        </div>
                        <div className="border rounded-lg p-4">
                            <div className="flex justify-between items-center">
                                <span className="text-slate-600">Stripe</span>
                                <span className="text-2xl font-bold text-slate-800">{dashboardData.paymentBreakdown.STRIPE}</span>
                            </div>
                            <div className="mt-2 h-2 bg-slate-100 rounded-full overflow-hidden">
                                <div className="h-full bg-green-500" style={{width: `${(dashboardData.paymentBreakdown.STRIPE / dashboardData.totalOrders) * 100}%`}}></div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-200">
                    <h2 className="text-lg font-semibold mb-4">Recent Activity</h2>
                    <div className="space-y-3">
                        {dashboardData.recentActivity.map((activity) => (
                            <div key={activity.id} className="flex items-center justify-between p-3 bg-slate-50 rounded-lg">
                                <div>
                                    <p className="font-medium">{activity.store}</p>
                                    <p className="text-xs text-slate-500">{new Date(activity.date).toLocaleString()}</p>
                                </div>
                                <div className="text-right">
                                    <p className="font-semibold text-green-600">{currency}{activity.total}</p>
                                    <span className="text-xs px-2 py-1 rounded-full bg-blue-100 text-blue-700">
                                        {activity.status}
                                    </span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    )
}